header {
    background: #3894f7;
    width: 100vw;
    height: 15vh;
    position: fixed;
    top: 0;
    left: 0;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 300%;
    font-weight: bold;
}

h1 {
    float: left;
}

.questions {
    position: fixed;
    overflow-y: scroll;
    width: 100vw;
    height: 85vh;
    padding-top: 5vw;
    top: 15vh;
    left: 0;
    background-color: #a3cfff;
}

.login {
    position: fixed;
    top: 15vh;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100vw;
    height: 85vh;
}

.logout {
    height: 40px;
    position: relative;
    top: -2px;
    float: right !important;
    margin-top: 4vh;
    margin-right: 1%;
}

.holder {
    display: block;
}

.holder-element {
    /* width: 50vw; */
}



