.Question {
    width: 90vw;
    background-color: white;
    text-align: center;
    margin-left: 5vw;
    margin-bottom: 5vh;
    padding-top: 1%;
    padding-bottom: 1%;
}

.title {
    width: 98%;
    background-color: #59a7f9;
    margin-left: 1%;
    height: 40px;
}

input {
    width: 90%;
    height: 40px;
}

.btn {
    height: 40px;
    position: relative;
    top: -2px;
}

.description {
    text-align: left !important;
    margin-left: 10px;
}
.hint {
    text-align: left !important;
    margin-left: 10px;
}



